<template>
  <div class="maincont">
    <header>
      <a href="javascript:history.back(-1)" class="back-off fl">
        <span class="glyphicon glyphicon-menu-left"></span>
      </a>
      <div class="head-mid">
        <h1>产品详情</h1>
      </div>
    </header>
    <div id="sliderA" class="slider">
      <img :src="baseUrl + list.icon" />
    </div>
    <table class="jia-len">
      <tr>
        <th>
          <strong class="orange">￥{{list.newPrice}}</strong>
        </th>
        <!-- <td class="flexX">
          <input type="button" value="-" @click="jian" />
          <input type="button" :value="digit" class="spinnerExample" />
          <input type="button" value="+" @click="jia" />
        </td> -->
      </tr>
      <tr>
        <td>
          <strong>{{list.product}}</strong>
          <p class="hui"></p>
        </td>
        <td align="right">
          <a href="javascript:;" class="shoucang">
            <span class="glyphicon glyphicon-star-empty"></span>
          </a>
        </td>
      </tr>
    </table>
    <div class="height2"></div>
    <h3 class="proTitle">商品规格</h3>
    <ul class="guige">
      <li v-for="item of 3" :key="item">
        <a href="javascript:;">{{item*50}}ML</a>
      </li>
      <div class="clearfix"></div>
    </ul>
    <!--guige/-->
    <div class="height2"></div>
    <div class="zhaieq">
      <a href="javascript:;" class="zhaiCur">商品简介</a>
      <a href="javascript:;">商品参数</a>
      <a href="javascript:;" style="background:none;">订购列表</a>
      <div class="clearfix"></div>
    </div>
    <div class="proinfoList" style="display: block;">
      <img :src="baseUrl + list.icon" width="636" height="822" />
    </div>
    <table class="jrgwc">
      <tr>
        <th>
          <a href="javascript:;">
            <span class="glyphicon glyphicon-home"></span>
          </a>
        </th>
        <td @click="addItem(list)">
          <router-link :to="goWhile+'?'+list._id" tag="button" >加入购物车</router-link>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import {mapActions} from 'vuex';

export default {
  methods:mapActions([
    'addItem'
  ]),
  data() {
    return {
      list: {},
      goWhile:'',
    };
  },
  
  mounted() {
    this.axios({
      url: "/api/list",
      params: { _page: 1, _limit: 10 }
    }).then(res => {
      let list0 = {};
      let msgQuery = Object.keys(this.$route.query)[0];
      res.data.data.forEach(function(value, index, selfArr) {
        if (msgQuery == value._id) {
          list0 = value;
        }
      });
      this.list = list0;
    });
    window.axios({
      url: "/api/user"
    }).then(res => {
      if (res.data.err === 0) {
        this.goWhile = '/car'
      } else {
        // 可以去购物车
        this.goWhile = '/login'
      }
    });
  }
};
</script>
<style scoped>
.slider {
  height: 400px;
  overflow: hidden;
}
/* .flexX {
  display: flex;
}
.flexX input {
  width: 24px;
  text-align: center;
} */
.jrgwc{
  position: fixed;
  bottom: 0;
  right: 0;
  line-height: 30px;
}
header{
  position: fixed;
  top: 0;
  left: 0;
}
</style>