<template>
  <div class="maincont">
    <div class="head-top">
      <img src="../assets/images/head.jpg" />
    </div>
    <!--head-top/-->
    <router-link to="" class="reg-login" tag="form" >
      <h3>
        已经有账号了？点此
        <router-link to="/login" class="orange">登陆</router-link>
      </h3>
      <div class="lrBox">
        <div class="lrList">
          <input type="text" placeholder="输入用户名" v-model="username"/>
        </div>
        <div class="lrList">
          <input type="text" placeholder="输入密码" v-model="password"/>
        </div>
        <br>
        <button @click="selectIcon">上传头像</button>
      </div>
      <div class="lrSub">
        <input type="submit" value="立即注册" @click="regBtn" />
      </div>
    </router-link>
    <input type="file" id="icon" style="display:none"/>
    <div class="height1"></div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      username:"",
      password:"",
      errMess:"",
    }
  },
  methods: {
    selectIcon() {
      let icon = document.getElementById("icon");
      console.log(icon)
      icon.click();
    },
    regBtn() {
      if (this.username && this.password) {
        let formData = new FormData();
        let input = document.getElementById("icon");
        formData.append("username", this.username);
        formData.append("password", this.password);
        formData.append("icon", input.files[0]);
        // console.log(this.username,this.password)
        window.axios({
          url: "/api/reg",
          method: "post",
          data: formData
        }).then(res => {
          if (res.data.err) {
            console.log(res.data.msg);
          } else {
            this.$router.push("/login");
          }
        }).catch((e) => {
          console.log(e)
        });
      } else {
        console.log("请输入用户名和密码");
      }
    }
  }
};
</script>
