            <input type="button" value="+" @click="changeItem({_id:item._id,number:1})" />
            <input type="button" value="×" />
          </td>
        </tr>
        <tr>
          <th colspan="4">
            <strong class="orange">小计：¥{{item.newPrice*item.num}}</strong>
          </th>
        </tr>
      </table>
    </div>
    <div class="height1"></div>
