import { mapState, mapActions } from "vuex";
import store from '../plugins/vuex';

export default {
  computed: mapState(["buyCar", "all"]),
  methods: {
    ...mapActions([
      "changeItem"
      // 'clearBuycar','removeItem'
    ]),
    btnGo(){
      console.log(1111111)
      window.axios({
        url: "/api/shopCar",
        method: "post",
        data: store.state.buyCar
      }).then(res => {
        console.log(res.data)
      }).catch((e) => {
        // console.log(111111111111)
      });
    }
  },
};
</script>
<style scoped>
.flexX {
  display: flex;
}
.flexX input {
  width: 24px;
  text-align: center;
}
.dingdanlist td h3 {
  font-size: 1.6rem;
}
</style>
