<template>
  <div id="app">
    <headerC v-if="$root.bHeader"></headerC>
    <router-view>展示区</router-view>
    <footerC v-if="$root.bFooter"></footerC>
  </div>
</template>

<script>
import headerC from "./components/headerC.vue";
import footerC from "./components/footerC.vue";

export default {
  name: "app",
  watch: {
    $route: {
      handler(newData) {
        let path = newData.path;
        if (/car|pay|success/.test(path)) {
          this.$root.bHeader = true;
          this.$root.bFooter = false;
          this.$root.bMsg = "购物车";
        }
        if (/home|user|list/.test(path)) {
          this.$root.bHeader = false;
          this.$root.bFooter = true;
        }
        if (/details/.test(path)) {
          this.$root.bHeader = false;
          this.$root.bFooter = false;
        }
        if (/login|reg/.test(path)) {
          this.$root.bHeader = true;
          this.$root.bFooter = true;
          if (/login/.test(path)) {
            this.$root.bMsg = "会员登录";
          }else{
            this.$root.bMsg = "会员注册";
          }
        }
      },
      immediate: true
    }
  },
  components: {
    headerC,
    footerC,
  }
};
</script>

