<template>
  <div class="maincont">
    <div class="head-top">
      <img src="../assets/images/head.jpg" />
    </div>
    <!--head-top/-->
    <router-link to class="reg-login" tag="form">
      <h3>
        还没有三级分销账号？点此
        <router-link to="/reg" class="orange">注册</router-link>
      </h3>
      <div class="lrBox">
        <div class="lrList">
          <input type="text" placeholder="输入用户名" v-model="username" />
        </div>
        <div class="lrList">
          <input type="text" placeholder="输入密码" v-model="password" />
        </div>
      </div>
      <!--lrBox/-->
      <div class="lrSub">
        <input type="submit" value="立即登录" @click="loginBtn" />
      </div>
    </router-link>
    <div class="height1"></div>
  </div>
</template>
<script>
import { mapState } from 'vuex';
import store from '../plugins/vuex';
export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  computed:mapState(['userState']),
  methods: {
    loginBtn() {
      if (this.username && this.password) {
        let formData = new FormData();
        formData.append("username", this.username);
        formData.append("password", this.password);
        window
          .axios({
            url: "/api/login",
            method: "post",
            data: formData
          })
          .then(res => {
            if (res.data.err) {
              console.log(res.data.msg);
            } else {
              this.$router.push("/user");
            }
          });
      } else {
        console.log("请输入用户名和密码");
      }
    }
  }
};
</script>
